<script>
  import { onDestroy, createEventDispatcher } from "svelte";
  import meetService from "./meetup";
  import Button from "../ui/Button.svelte";

  export let id;

  let meetup;
  const dispatch = createEventDispatcher();

  const unsubs = meetService.subscribe((meets) => {
    meetup = meets.find((i) => (i.id = id));
  });

  onDestroy(() => {
    unsubs();
  });
</script>

<section>
  <div class="image">
    <img src={meetup.imageUrl} alt={meetup.title} />
  </div>
  <div class="contetn">
    <h1>{meetup.title}</h1>
    <h2>{meetup.subtitle} - {meetup.address}</h2>
    <p>{meetup.description}</p>
    <Button type="button" mode="outline" on:click={() => dispatch("close")}
      >Cancel</Button
    >
    <Button href="mailto:{meetup.email}">Contact</Button>
  </div>
</section>

