const cardService = {
  subscribe: meets.subscribe,
  favorite: (id) => {
    meets.update((items) => {
      items.map((r) => {
        if (r.id === id) r.isFav = !r.isFav;
      });
      return [...items];
    });
  },
  add: (item) => {
    item = {
      ...item,
      id: Math.random().toString,
      isFav: false,
      imageUrl: "http://placeimg.com/640/360/" + item.imageUrl,
    };
    meets.update((items) => {
      return [item, ...items];
    });
  },
  update: (id, data) => {
    meets.update((items) => {
      return items.map((i) => {
        if (i.id === id) i = { ...i, ...data };
        return i;
      });
    });
  },
  remove: (id) => {
    meets.update((items) => {
      return items.filter((i) => i.id !== id);
    });
  },
};

export default cardService;
