<script>
  import { createEventDispatcher } from "svelte";
  import validator from "validator";
  import meetService from "./meetup";
  import TextInput from "../ui/TextInput.svelte";
  import Button from "../ui/Button.svelte";
  import Modal from "../ui/Modal.svelte";

  export let id = null;

  let title = "";
  let subtitle = "";
  let address = "";
  let email = "";
  let imageUrl = "";
  let description = "";
  if (id) {
    const u = meetService.subscribe((meets) => {
      const meet = meets.find((i) => i.id === id);
      title = meet.title;
      subtitle = meet.subtitle;
      address = meet.address;
      email = meet.email;
      imageUrl = meet.imageUrl;
      description = meet.description;
    });
    u();
  }

  $: tValid = !validator.isEmpty(title);
  $: sValid = !validator.isEmpty(subtitle);
  $: aValid = !validator.isEmpty(address);
  $: eValid = validator.isEmail(email);
  $: iValid = validator.isInt(imageUrl) && !validator.isEmpty(imageUrl);
  $: dValid = !validator.isEmpty(description);
  $: fValid = tValid && sValid && aValid && eValid && iValid && dValid;

  const dispatch = createEventDispatcher();

  function onSubmit() {
    const data = {
      title,
      subtitle,
      address,
      email,
      imageUrl,
      description,
    };
    if (id) {
      meetService.update(id, data);
    } else {
      meetService.add(data);
    }
    dispatch("submit");
  }

  function remove() {
    meetService.remove(id);
    cancel();
  }

  function cancel() {
    dispatch("cancel");
  }
</script>

<Modal title="Create MeetUp" on:cancel>
  <form on:submit|preventDefault={onSubmit}>
    <TextInput
      title="Title"
      id="title"
      value={title}
      type="text"
      validtyMessage="Please enter right email format"
      valid={tValid}
      on:input={(e) => (title = e.target.value)}
    />
    <TextInput
      title="Subtitle"
      id="subtitle"
      value={subtitle}
      type="text"
      validtyMessage="Please fill the field"
      valid={sValid}
      on:input={(e) => (subtitle = e.target.value)}
    />
    <TextInput
      title="Address"
      id="address"
      value={address}
      type="text"
      valid={aValid}
      validtyMessage="Please fill the field"
      on:input={(e) => (address = e.target.value)}
    />
    <TextInput
      title="Number for image"
      id="image"
      value={imageUrl}
      type="number"
      valid={iValid}
      validtyMessage="Please fill the field and it must be number"
      on:input={(e) => (imageUrl = e.target.value)}
    />
    <TextInput
      title="Email"
      id="email"
      value={email}
      type="text"
      valid={eValid}
      validtyMessage="Check email format"
      on:input={(e) => (email = e.target.value)}
    />
    <TextInput
      title="Description"
      id="description"
      value={description}
      type="textarea"
      valid={dValid}
      validtyMessage="Please fill the field"
      on:input={(e) => (description = e.target.value)}
    />
    <Button mode="outline" on:click={cancel}>Cancel</Button>
    {#if id}
      <Button mode="outline" on:click={remove}>Delete</Button>
    {/if}
    <Button type="submit" disabled={!fValid}>Publish</Button>
  </form>
  <div slot="footer">
    <small><i>㊢ © Please respect to our guidlines</i></small>
  </div>
</Modal>

<style>
  form {
    width: 100%;
  }
</style>
