<script>
  export let title,
    id,
    type,
    value,
    validtyMessage = null;
  export let valid = true;
  let touched = false;
</script>

{#if type === "textarea"}
  <div class="form-control">
    <label for={id}>{title}</label>
    <textarea
      class:invalid={!valid && touched}
      name={id}
      {id}
      {value}
      rows="4"
      on:input
      on:blur={() => (touched = true)}
    />
  </div>{:else}
  <div class="form-control">
    <label for={id}>{title}</label>
    <input
      class:invalid={!valid && touched}
      type="text"
      {id}
      {value}
      on:input
      on:blur={() => (touched = true)}
    />
  </div>
  {#if validtyMessage && !valid && touched}
    <small class="errorMessage">{validtyMessage}</small>
  {/if}
{/if}

<style>
  .form-control,
  input,
  textarea {
    width: 100%;
  }

  input {
    margin-bottom: 0;
  }

  .invalid {
    border: 1px solid rgb(136, 38, 38);
  }

  .errorMessage {
    display: block;
    color: rgb(136, 38, 38);
    margin-bottom: 0.5rem;
  }
</style>
