<script>
  // import { onDestroy } from "svelte";
  import meets from "./meetup/meetup";
  import Header from "./ui/Header.svelte";
  import MeetUpGrid from "./meetup/MeetUpGrid.svelte";
  import EditMeetup from "./meetup/EditMeetup.svelte";
  import Detail from "./meetup/Detail.svelte";

  let mode = 0;
  let page = "overview";
  let editId;

  function more(e) {
    page = e.detail;
  }

  function edit(e) {
    mode = 1;
    editId = e.detail;
  }
</script>

<Header on:click={() => (mode = 1)} />

<main>
  {#if page === "overview"}
    {#if mode == 1}
      <EditMeetup
        id={editId}
        on:submit={(mode = 0)}
        on:cancel={() => ((mode = 0), (editId = 0))}
      />
      <hr />
    {/if}
    <MeetUpGrid meetups={$meets} on:more={more} on:edit={edit} />
  {:else}
    <Detail id={page} on:close={() => (page = "overview")} />
  {/if}
</main>

<style>
  main {
    margin-top: 4rem;
  }
</style>
